/* http://meyerweb.com/eric/tools/css/reset/ 
v2.0 | 20110126
License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
body, input, button {
  font-family: 'Cabin', sans-serif;
  color: #3C3C3C;
}

a {
  color: #DE1100;
}

input {
  padding: 0.2rem;
  border: 0;
  border-radius: 0.2rem;
  outline: 0;
}

button {
  background: #ffffff;
  color: #DE1100;
  border: 0.1rem solid #DE1100;
  font-weight: 600;
  border-radius: 0.2rem;
  padding: 0.2rem 1rem 0.2rem 1rem;
}
.movie-section_rating{
    padding: 0.5rem;
}

[class^="rating"]{
    padding-right: 0.5rem;
    color: black;
}

.far.active{
    font-weight: 600;
}

.fa-smile.active{
    color: #008B0B;
}

.fa-meh.active{
    color: #FFD500;
}

.fa-frown.active{
    color: #00A2EE;
}
.movie-section_favorite{
    padding: 0.5rem;
    text-align: right;
    cursor: pointer;
}

.movie-section_button {
    border: 0;
    padding: 0;
}
.movie-section {
    box-shadow: 1px 2px 35px -13px rgba(0,0,0,0.75);
    padding: 0.2;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    min-height: 100%;
}

.movie-section_info{
    padding: 0 .5rem;
}

.movie-section_poster {
    width: 80%;
    display: block;
    margin: 0 auto;
    padding: 0.8rem 0.2rem;
    border-radius: 0.5rem;
}

.movie-section_title{
    text-align:center;
    font-weight: 600;
    color: #DE1100;
}

.movie-section_delete {
    padding: 0.5rem;
    text-align: right;
    cursor: pointer;
}

.modal-header{
    background: #DE1100;
    padding: 1rem;
    color: #ffffff;
    font-weight: 600;
    font-size: 1.5rem;
    text-align: center;
}

.modal-body{
    padding: 1rem;
}

.modal-body ul li {
    padding: 0.5rem 0;
    cursor: pointer;
}

.modal-body ul li:not(:last-child){
    border-bottom: 0.1rem dashed #DE1100;
}
.movies{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-row-gap: 1rem;
    grid-column-gap: 1rem;
    padding: 1rem .5rem 1rem .5rem;
}

@media screen and (min-width: 762px){
    .movies {
        grid-template-columns: repeat(4, 1fr);
    }
}
#search-form {
  padding: 0.5rem;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  background: #DE1100;
}

.search-form_input {
    width: 80%;
}

@media screen and (min-width: 762px){
    .search-form_input {
        width: 90%;
    }
}

#my-collections{
    display: grid;
    grid-template-columns: 30% 1fr;
    grid-template-rows: 1fr auto;
}

.my-collections_create {
    display: block;
    margin: 0.5rem;
    outline: none;
}

.my-collections_nav{
    grid-column: 1 / span 2;
}

.my-collections_aside{
    padding: 1rem 0.5rem;
}

.my-collections_aside li {
    cursor: pointer;
    padding: 0.5rem 0rem;
    word-break: break-all;
}

.my-collections_aside li:not(:last-child){
    border-bottom: 0.05rem dashed black;
}

#my-collections_create-form {
    padding: 0.5rem;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    background: #DE1100;
}

.my-collections_input {
    width: 80%;
}

.my-collections_delete{
    cursor: pointer;
    padding-right: .5rem;
    transition: all .2s ease-in;
}

.my-collections_delete:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.my-collections-nav--active{
    font-weight: 600;
    color: #DE1100;
    font-size: 1.2rem;
}

@media screen and (min-width: 762px){
    .my-collections_input {
        width: 90%;
    }
}

.movie-info {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr;
    padding: 2rem;
}

.movie-info_poster {
    width: 80%;
    justify-self: left;
    border-radius: 0.3rem;
}

.movie-info_title {
    font-size: 1.5rem;
    color: #de1100;
    font-weight: 600;
}

.movie-info_info li:not(:last-child) {
    padding-bottom: 1rem;
}

.movie-info_info h3, .movie-info_director p, .movie-info_release_date p {
    display: inline-block;
}

.movie-info_info h3{
    padding-right: 0.1rem;
    font-weight: 600;
}

@media screen and (min-width: 762px){
    .movie-info {
        grid-template-columns: 1fr 2fr;
    }
}

.error-message{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    margin-top: 10rem;
}

.fa-skull-crossbones{
    font-size: 5rem;
    color: #DE1100;
    padding-bottom: 1rem;
}

.error-message_message{
    font-size: 1.5rem;
    font-weight: 600;
}
#navigator {
    box-shadow: -1px 9px 14px -12px rgba(0,0,0,0.75);
}

.navigator_ul {
    padding-bottom: 0.8em;
    padding: 0.5rem;
}

.navigator_li {
    display: inline-block;
    margin-right: 0.8rem;
    transition: all .2s ease-in;
}

.navigator_a {
    text-decoration: none;
}

.navigator_a--selected {
    text-decoration: none;
    font-weight: 600;
}

.navigator_li:hover{
    transform: scale(1.1);
    -webkit-transform: scale(1.1);
}
